import React, { useContext, useRef, useState } from "react";

import LoginContext from "../../store/loginContext";
import langContextObj from "../../store/langContext";
import { images } from "../../constants";
import Input from "../UI/input/Input";
import Button from "../UI/button/Button";
import { useTranslation } from "react-i18next";
import classes from "./Login.module.scss";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { JsxEmit } from "typescript";
import { Center, Heading } from "@chakra-ui/react";

function LoginBox() {
  const loginCtx = useContext(LoginContext);
  const [loading,setLoading]=useState<boolean>(false)
  const langCtx = useContext(langContextObj);
  const emailRef = useRef<HTMLInputElement>(null);
  const passwordRef = useRef<HTMLInputElement>(null);
  const errorMessageRef = useRef<HTMLSpanElement>(null!);
  const navigate = useNavigate();
  const { t } = useTranslation();

  async function loginHandler(e: React.FormEvent) {
    e.preventDefault();
    const emailValue = emailRef.current?.value.trim();
    const passwordValue = passwordRef.current?.value.trim();


    // Email validation
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(emailValue || '')) {
      errorMessageRef.current?.setAttribute(
        "style",
        "display: inline-block; opacity: 1"
      );
      errorMessageRef.current.innerText = "Invalid email format";
      emailRef.current?.focus();
      return;
    } else {
      emailRef.current?.blur(); // Remove focus if the error is resolved
    }

    // Password validation
    const passwordRegex = /^(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{6,})/;
    if (!passwordRegex.test(passwordValue || '')) {
      errorMessageRef.current?.setAttribute(
        "style",
        "display: inline-block; opacity: 1"
      );
      errorMessageRef.current.innerText =
        "Password must be at least 6 characters, contain at least one uppercase letter, one number, and one special character";
      passwordRef.current?.focus();
      return;
    } else {
      passwordRef.current?.blur();
    }

    try {
      setLoading(true)
      await axios.post(process.env.REACT_APP_API_URL+'/api/users/signin', {email: emailRef.current?.value, password: passwordRef.current?.value }).then((res) => {
        localStorage.setItem("isUser", JSON.stringify(res?.data?.user));
        loginCtx.toggleLogin();
        navigate("/");
      }).catch((err) => {
        errorMessageRef.current?.setAttribute(
          "style",
          "display: inline-block; opacity: 1"
        );
        errorMessageRef.current.innerText = err?.response?.data?.message || err?.response?.data?.errors[0] || "Something went wrong!"
      })
setLoading(false);
    } catch (err) {
      console.log(err)
    }
  }

  return (
    <div
      className={`${classes.container} ${
        langCtx.lang === "hi" ? classes.rtl : ""
      }`}
    >
      <div className={classes.loginBox}>
        <div className={classes.logo}>
         <Center> <Heading color={"red"} textDecoration={"underline"} mt={10} mb={14} size={"md"}>Aron Web Solutions</Heading></Center>
          {/* <img src={images.logo} alt="aronweb" /> */}
        </div>
        <h2 className={classes.title}>{t("loginPage")}</h2>
        <form onSubmit={loginHandler}>
          <Input
            ref={emailRef}
            type={"email"}
            id={"Email"}
            placeholder={"email"}
          />
         
          <Input
          ref={passwordRef}
            type={"password"}
            id={"pass"}
          />
           <span ref={errorMessageRef} className={classes.errorMessage}>
          </span>
          <Button type="submit">{!loading?t("login"):"Loading..."}</Button>
          <div style={{display:"flex",gap:"25px",justifyContent:"space-between"}}> <Link className={classes.forgat_pass} to="/">
            {t("forgetPass")}
          </Link>
          <Link className={classes.forgat_pass} to="/register">
            {"Don't have account?"}
          </Link></div>
         
        
        </form>
      </div>

      <div className={classes.keyPic}>
        <img
          src={require("../../assets/images/Revenue-cuate.svg").default}
          alt="illustrator key"
        />
      </div>
    </div>
  );
}

export default LoginBox;
